<template>
<!-- 首页 -->
  <div id="box">
    <el-menu theme="light" :default-active="activeIndex" class="el-menu-demo bg-purple-light" mode="horizontal" @select="handleSelect">
  <el-menu-item index="9">新闻</el-menu-item>
  <el-menu-item index="8">文字</el-menu-item>
  <el-menu-item index="7">美女</el-menu-item>
  <el-menu-item index="6">排行</el-menu-item>
  <el-menu-item index="5">声音</el-menu-item>
  <el-menu-item index="4">图片</el-menu-item>
  <el-menu-item index="3">视频</el-menu-item>
  <el-menu-item index="2">24秒</el-menu-item>
  <el-menu-item index="1">全部</el-menu-item>
  <!-- <el-menu-item index="9" class="el-icon-plus">每日头条</el-menu-item> -->
</el-menu>
  </div>
</template>
<script>
import '../assets/jquery-1.11.3.min.js'
import './newvue/new.js'
  export default {
    data() {
      return {
        activeIndex: '1'
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        if(key==4){
        function formatterDateTime() {
            var date=new Date()
            var month=date.getMonth() + 1
                  var datetime = date.getFullYear()
                          + ""// "年"
                          + (month >= 10 ? month : "0"+ month)
                          + ""// "月"
                          + (date.getDate() < 10 ? "0" + date.getDate() : date
                                  .getDate())
                          + ""
                          + (date.getHours() < 10 ? "0" + date.getHours() : date
                                  .getHours())
                          + ""
                          + (date.getMinutes() < 10 ? "0" + date.getMinutes() : date
                                  .getMinutes())
                          + ""
                          + (date.getSeconds() < 10 ? "0" + date.getSeconds() : date
                                  .getSeconds());
                  return datetime;
              }
          // 搞笑图片
          $.ajax({
            type: 'post',
              url: 'https://route.showapi.com/341-2',
              dataType: 'jsonp',
              data: {
                  "showapi_timestamp": formatterDateTime(),
                  "showapi_appid": '39016', //这里需要改成自己的appid
                  "showapi_sign": '19e655b2f09a4c95a64fdc429f1e18bf',
                  'page': '1',
                  'maxResult': '10'  //这里需要改成自己的应用的密钥secret，
              },
              jsonp: 'jsonpcallback', //这个方法名很重要,不能改变
              beforeSend: function(){
                console.log('请求之前')
              },
              error: function(XmlHttpRequest, textStatus, errorThrown) {
                  alert("操作失败!");
              },
              success: function(result) {
                  // console.log(result.showapi_res_body.contentlist) //console变量在ie低版本下不能用
                  // console.log(Event)
                  console.log('请求成功')
                  Event.$emit('eventName',result.showapi_res_body.contentlist)
                  Event.$emit('allpages',result.showapi_res_body.allPages)
              }
          });
        }
        if(key==7){
          // 美女图片
          function formatterDateTime() {
            var date=new Date()
            var month=date.getMonth() + 1
                  var datetime = date.getFullYear()
                          + ""// "年"
                          + (month >= 10 ? month : "0"+ month)
                          + ""// "月"
                          + (date.getDate() < 10 ? "0" + date.getDate() : date
                                  .getDate())
                          + ""
                          + (date.getHours() < 10 ? "0" + date.getHours() : date
                                  .getHours())
                          + ""
                          + (date.getMinutes() < 10 ? "0" + date.getMinutes() : date
                                  .getMinutes())
                          + ""
                          + (date.getSeconds() < 10 ? "0" + date.getSeconds() : date
                                  .getSeconds());
                  return datetime;
              }
          $.ajax({
            type: 'post',
            url: 'https://route.showapi.com/197-1',
            dataType: 'jsonp',
            data: {
                "showapi_timestamp": formatterDateTime(),
                "showapi_appid": '39016', //这里需要改成自己的appid
                 "showapi_sign": '19e655b2f09a4c95a64fdc429f1e18bf',  //这里需要改成自己的应用的密钥secret，
                 "page": "1",
                 // 随机
                 // "rand": '1',
                 "num": '10'
                },
            jsonp: 'jsonpcallback', //这个方法名很重要,不能改变
            error: function(XmlHttpRequest, textStatus, errorThrown) {
                 alert("操作失败!");
                },
            success: function(result) {
                console.log(result.showapi_res_body.newslist) //console变量在ie低版本下不能用
                Event.$emit('meinvlist',result.showapi_res_body.newslist)
                Event.$emit('meinvpages',1000)
                }
            })
        }
      }
    },
    mounted: function() {
      Event.$on('pages', function(pages){
        console.log(pages)
        function formatterDateTime() {
            var date=new Date()
            var month=date.getMonth() + 1
                  var datetime = date.getFullYear()
                          + ""// "年"
                          + (month >= 10 ? month : "0"+ month)
                          + ""// "月"
                          + (date.getDate() < 10 ? "0" + date.getDate() : date
                                  .getDate())
                          + ""
                          + (date.getHours() < 10 ? "0" + date.getHours() : date
                                  .getHours())
                          + ""
                          + (date.getMinutes() < 10 ? "0" + date.getMinutes() : date
                                  .getMinutes())
                          + ""
                          + (date.getSeconds() < 10 ? "0" + date.getSeconds() : date
                                  .getSeconds());
                  return datetime;
              }
          $.ajax({
            type: 'post',
              url: 'https://route.showapi.com/341-2',
              dataType: 'jsonp',
              data: {
                  "showapi_timestamp": formatterDateTime(),
                  "showapi_appid": '39016', //这里需要改成自己的appid
                  "showapi_sign": '19e655b2f09a4c95a64fdc429f1e18bf',
                  'page': pages,
                  'maxResult': '10'  //这里需要改成自己的应用的密钥secret，
              },
              jsonp: 'jsonpcallback', //这个方法名很重要,不能改变
              beforeSend: function(){
                console.log('请求之前')
              },
              error: function(XmlHttpRequest, textStatus, errorThrown) {
                  alert("操作失败!");
              },
              success: function(result) {
                  // console.log(result.showapi_res_body.contentlist) //console变量在ie低版本下不能用
                  // console.log(Event)
                  console.log('请求成功')
                  console.log(result)
                  document.body.scrollTop = 0
                  Event.$emit('eventName',result.showapi_res_body.contentlist)
                  Event.$emit('allpages',result.showapi_res_body.allPages)
              }
          })
      }.bind(this));
// 美女图片
      Event.$on('meinvpagesa',function(pagesmeinv){
        // console.log(pagesmeinv+"美女图片 ")
         function formatterDateTime() {
            var date=new Date()
            var month=date.getMonth() + 1
                  var datetime = date.getFullYear()
                          + ""// "年"
                          + (month >= 10 ? month : "0"+ month)
                          + ""// "月"
                          + (date.getDate() < 10 ? "0" + date.getDate() : date
                                  .getDate())
                          + ""
                          + (date.getHours() < 10 ? "0" + date.getHours() : date
                                  .getHours())
                          + ""
                          + (date.getMinutes() < 10 ? "0" + date.getMinutes() : date
                                  .getMinutes())
                          + ""
                          + (date.getSeconds() < 10 ? "0" + date.getSeconds() : date
                                  .getSeconds());
                  return datetime;
              }
        $.ajax({
            type: 'post',
            url: 'http://route.showapi.com/197-1',
            dataType: 'jsonp',
            data: {
                "showapi_timestamp": formatterDateTime(),
                "showapi_appid": '39016', //这里需要改成自己的appid
                 "showapi_sign": '19e655b2f09a4c95a64fdc429f1e18bf',  //这里需要改成自己的应用的密钥secret，
                 "page": pagesmeinv,
                 // 随机
                 // "rand": '1',
                 "num": '10'
                },
            jsonp: 'jsonpcallback', //这个方法名很重要,不能改变
            error: function(XmlHttpRequest, textStatus, errorThrown) {
                 alert("操作失败!");
                },
            success: function(result) {
                console.log(result.showapi_res_body.newslist) //console变量在ie低版本下不能用
                 document.body.scrollTop = 0
                Event.$emit('meinvlist',result.showapi_res_body.newslist)
                Event.$emit('meinvpages',1000)
                }
            });
      }.bind(this));
      this.handleSelect(4);
    }
  }
</script>

<style scoped>
  .el-menu--horizontal .el-menu-item{
    float: right;
    width: 10%;
    text-align: center;
  }
  .el-menu {
    border-radius: 2px;
    list-style: none;
    position: fixed;
    margin: 0;
    padding-left: 0;
    width: 100%;
    z-index: 156;
    top: 0px;
    background-color: #eef1f6;
}
</style>


