<template>
	<div id="box1">
    <el-row :gutter="10">
      <el-col :xs="17" :sm="17" :md="17" :lg="17">
        <div class="grid-content bg-purple" v-for="txt in text">
           <span><strong>{{txt.title}}</strong><i>{{txt.ct}}</i></span>
           <div class="img">
             <img :src="txt.img || txt.picUrl" alt="">
           </div>
        </div>
      </el-col>
      <el-col :xs="7" :sm="7" :md="7" :lg="7">

        <div class="grid-content ylxx bg-purple-light">
            <h4>各位客官如果觉得开心了，请用支付宝赞助一下，谢谢！</h4>
            <img src="https://raw.githubusercontent.com/qixiaofeidadi/Funnyincoming/master/static/img/QQ%E6%88%AA%E5%9B%BE20170924185153.2d00697.png" height="389" width="463" alt="">
        </div>
          <div class="grid-content ylxx bg-purple-light">
            <h4>平台最受欢迎的休闲娱乐软件、搞笑休闲平台，网络热辣段子发源地！</h4>
        </div>
      </el-col>
    </el-row>
	</div>
</template>
<script>
  import './newvue/new.js'
  export default{
    data() {
      return {
          text: []
      }
    },
    mounted() {
      // 搞笑图片
      Event.$on('eventName',function(a){
        this.text = a
      }.bind(this));
      Event.$on('meinvlist',function(b){
        this.text = b
      }.bind(this))
    }
  }
</script>
<style scoped>
  .el-col {
    border-radius: 4px;
  }
  .bg-purple-dark {
    background: #99a9bf;
  }
  .bg-purple {
    background: #d3dce6;
  }
  .bg-purple-light {
    background: #e5e9f2;
  }
  .grid-content {
    border-radius: 4px;
    min-height: 36px;
    height: auto;
    vertical-align: middle;
    text-align:center;
  }
.grid-content span{
  width: 60%;
  height: auto;
  display: inline-block;
  font-size: 1.25rem;
  line-height: 30px;
  /* padding:0px 14px; */
  text-align: center;
  background-color: #fff;
}
.grid-content span strong{
  float: left;
}
.grid-content span i{
  font-size: 12px;
  float: right;
}
  .grid-content .img{
    width: 60%;
    height: auto;
    margin: 0 auto;
    background-color: #fff;
    border-bottom:30px solid #e8e8e8;
  }
  .grid-content .img img{
    margin-bottom: 60px;
    max-width: 90%;
  }
  #box1{
    position: relative;
    top:60px;
  }
  .zhichi{
    height: 50px;
    line-height: 50px;
  }
  .ylxx{
    line-height: 40px;
  }
  .ylxx img{
    width: 250px;
    height: 220px;
  }
</style>


