<template>
	<div class="block">
<!-- 	  <span class="demonstration">大于 7 页时的效果</span> -->
	  <el-pagination
      @current-change="handleCurrentChange"
      :current-page="currentPage4"
	    layout="prev, pager, next"
	    :total="number">
	  </el-pagination>
	</div>
</template>
<script>
import '../newvue/new.js'
	export default {
		data() {
			return {
				currentPage4: 1,
				number: 0,
				pages: ''
			}
		},
		 methods: {
	      handleCurrentChange(val) {
	        console.log('当前页',val); 
	         // Event.$emit('pages', val)
	         Event.$emit(this.pages, val)
      }
	},
	mounted() {
		
		// 搞笑图片
		Event.$on('allpages',function(allpage){
			this.number = allpage * 10
			this.pages = 'pages'
		}.bind(this));
		// 美女图片
		Event.$on('meinvpages',function(meinvpages){
			this.number = meinvpages
			console.log(meinvpages+'页码')
			this.pages = 'meinvpagesa'
		}.bind(this));
	}
}
</script>
<style scoped>
.block {
    position: relative;
    top: 80px;
    bottom: 58px;
}
.el-pagination {
    white-space: nowrap;
    padding: 2px 530px;
    color: #48576a;
}
</style>


